    #t_1_1.titulo-segundo
      h2 1.1  Patronaje: definicin

    p.mb-5 El patronaje es un sistema por el cual se comienza la fabricacin de una prenda de vestir partiendo de un diseo determinado. ste consiste en desglosar en piezas separadas las reas del cuerpo humano a cubrir y as, una vez cortadas en tela y unidas las piezas se obtiene el modelo o prototipo del producto diseado (Carrera de Diseo y Gestin en Moda, 2015a).

    .row.justify-content-center
      .col-md-5.mb-4
        figure
          img(src="@/assets/curso/tema1-imagen2.svg")
      .col-md-5.mb-4
        figure
          img(src="@/assets/curso/tema1-imagen3.svg")
      .col-md-10.mb-5
        p.mb-4 El ejercicio del patronaje comienza por el anlisis y toma de medidas del cuerpo humano, su constitucin, dimensiones, proporciones y tipologa, para as, interpretar la construccin adecuada de cada molde o pieza y cumplir con los requerimientos de la prenda (Gutirrez Rengifo et al., 2011).
        .row.justify-content-center
          .col-lg-6
            a.anexo.mb-4(href="https://youtu.be/8aICqukm-Ng" target="_blank")
              .anexo__icono
                img(src="@/assets/curso/icono-youtube.svg")
              .anexo__texto
                p
                  strong Video.
                  |   Fines del Patronaje.
      .col-md-10.mb-4
        figure.mb-0
          img(src="@/assets/curso/tema1-imagen4.jpg")
        .cajon.color-acento-contenido.px-5.py-4.mb-4
          p.px-5.mb-0 Luego, las medidas son traducidas o dibujadas sobre un papel (en el caso del patronaje manual) a lo que se llama patrn o molde de la pieza y el conjunto de todas las piezas recibe el nombre de patrn del modelo. El patronaje se vale de moldes bsicos previamente validados sobre el cuerpo humano (modelados), para desarrollar los diferentes diseos. Este proceso le permite al patronista (profesional encargado del patronaje), adelantar trabajo y as no tener que trazar desde cero cada vez que se requiera un diseo.
      .col-md-9
        figure.mb-5
          img(src="@/assets/curso/tema1-imagen5.svg")
        p.mb-5 El patronista debe interpretar correctamente el boceto o diseo del modelo a desarrollar, crear la secuencia de transformaciones del patrn bsico, incluyendo mtodos de trazado y construccin de patrones adicionales para lograr el patrn de la prenda a confeccionar.
        .row.justify-content-center
          .col-md-3
            figure
              img(src="@/assets/curso/tema1-imagen6.svg")
          .col-md-8.offset-md-1
            .cajon.color-primario.p-5.mb-4
              p.mb-0 Con el fin de agilizar el proceso de patronaje industrializado y evitar construir un patrn para cada talla, se recurre al proceso de escalado, que consiste en aumentar o disminuir las medidas de cada una de las piezas segn el tamao o talla del cuerpo al que corresponde.
            p.mb-0 Una vez finalizados los patrones, dependiendo del ancho til de la tela, se realiza un trazado preliminar con el fin de calcular el consumo de materiales y registrar la informacin en la ficha tcnica del producto, la cual ser suministro de las reas financieras y de produccin.
        figure.mt-2.mb-5
          figcaption Fuente: Tcnicas de patronaje. Tomo I: Mujer (Carrera de Diseo y Gestin en Moda, 2015a)
        figure.mb-5
          img(src="@/assets/curso/tema1-imagen7.jpg")
          figcaption Fuente: Elaboracin propia en Optitex Marker.
        p.mb-4 En definitiva, el patronaje est presente en toda la cadena productiva del vestuario, iniciando con la creacin de los patrones a partir del concepto creativo del diseador, conservando su originalidad, asegurando las correctas medidas y proporciones, suministrando la informacin necesaria para los clculos financieros y productivos, constituyndose en un aliado fundamental que enlaza el departamento creativo con el de produccin.
