    #t_1_2.titulo-segundo
      h2 1.2  Vocabulario tcnico de patronaje

    p.mb-5 Cada actividad tcnica emplea terminologa especfica, es decir, palabras que se entienden en el contexto de la profesin. A continuacin, se presenta vocabulario relacionado con el patronaje:

    .row.justify-content-center.mb-5
      .col-md-3.mb-md-0.mb-5
        figure.mb-0
          img(src="@/assets/curso/tema1-imagen8.svg")
      .col-md-9
        .tarjeta.tarjeta--gris.p-4.mb-0
          LineaTiempoC.color-acento-contenido(text-small)
            .row.px-5.py-4(titulo="Acabado")
              .col-12
                p.mb-0.px-5 Hace referencia a los diferentes procesos industriales para perfeccionar un producto. El acabado en los tejidos y prendas comprende el blanqueado, teido, encogido, estirado, estampado, satinado y planchado, entre otros.
            .row.px-5.py-4(titulo="Almilla")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen9.svg")
              .col-lg-7
                p.mb-0.px-5 Es tambin conocida como canes, es un corte superior en la espalda o el frente de la camisa o la blusa.
            .row.px-5.py-4(titulo="ngulo")
              .col-12
                p.mb-0.px-5 Abertura entre dos lneas o planos que se cortan entre s, se expresa en grados, por ejemplo, un ngulo recto o perpendicular equivale a 90.
            .row.px-5.py-4(titulo="Aplome")
              .col-12
                p.mb-0.px-5 Es la lnea que indica el hilo de la tela o quiebre de una prenda, se traza sobre el molde indicando la posicin en que se cortar sobre la tela, esta lnea permite cortes alineados, evitando que las partes se tuerzan al momento del trazado.
            .row.px-5.py-4(titulo="rea")
              .col-12
                p.mb-0.px-5 Superficie comprendida dentro de un permetro, as el rea se refiere a la superficie de cada una de las piezas, la suma de stas ser el rea del modelo.
            .row.px-5.py-4(titulo="Banda de cuello")
              .col-12
                p.mb-0.px-5 Tambin llamada pie de cuello, es la parte inferior del cuello camisero.
            .row(titulo="Bisectriz")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen10.svg")
              .col-lg-7
                p.mb-0.px-5 Recta que pasa por el vrtice de un ngulo y lo divide en dos partes iguales.
            .row.px-5.py-4(titulo="Blusa")
              .col-12
                p.mb-0.px-5 Prenda superior de tela fina, similar a la camisa, que usan las mujeres y los nios.
            .row.px-5.py-4(titulo="Bocamanga")
              .col-12
                p.mb-0.px-5 Parte inferior de las mangas, tambin conocida como bajo o ruedo.
            .row.px-5.py-4(titulo="Bolsillo")
              .col-12
                p.mb-0.px-5 Pieza que va cosida y forma una bolsa utilizada para guardar objetos en las prendas y accesorios, puede ser sobrepuesto (tipo parche) o interno (de ribete o lateral).
            .row.px-5.py-4(titulo="Boca de bolsillo")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen11.svg")
              .col-lg-7
                p.mb-0.px-5 Es la parte abierta del bolsillo, por la cual se introduce la mano.
            .row.px-5.py-4(titulo="Corte")
              .col-12
                p.mb-0.px-5 Separacin de una pieza en dos diferentes.
            .row.px-5.py-4(titulo="Costado")
              .col-12
                p.mb-0.px-5 Lado en el que se unen las piezas delanteras con posteriores.
            .row.px-5.py-4(titulo="Costura")
              .col-12
                p.mb-0.px-5 Serie de puntadas que une dos piezas cosidas, en patronaje se denomina costura a la margen que se adiciona para este fin.
            .row.px-5.py-4(titulo="Cuello")
              .col-12
                p.mb-0.px-5 Pieza superior de la camisa que rodea el cuello. Para la elaboracin del patrn se requieren las medidas de escote delantero y escote espalda.
            .row.px-5.py-4(titulo="Cuerpo")
              .col-12
                p.mb-0.px-5 Parte de las prendas superiores que va desde los hombros y hasta la cintura.
            .row.px-5.py-4(titulo="Curva")
              .col-12
                p.mb-0.px-5 Lnea que se aparta de manera continua de la direccin recta, sin formar ngulos.
            .row.px-5.py-4(titulo="Curva cncava")
              .col-12
                p.mb-0.px-5 Curva o superficie que se asemeja al interior de una circunferencia o una esfera (hacia adentro).
            .row.px-5.py-4(titulo="Curva convexa")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen12.svg")
              .col-lg-7
                p.mb-0.px-5 Curva o superficie que se asemeja al exterior de una circunferencia o esfera (hacia afuera).
            .row.px-5.py-4(titulo="Delantero")
              .col-12
                p.mb-0.px-5 Pieza que constituye la parte frontal de las prendas.
            .row.px-5.py-4(titulo="Desarrollo de modelos")
              .col-12
                p.mb-0.px-5 Operaciones necesarias para la obtencin de los patrones vestibles a partir de los patrones base.
            .row.px-5.py-4(titulo="Dobladillo")
              .col-12
                p.mb-0.px-5 Borde de la tela doblada dos veces sobre si misma asegurada con puntadas a mano o a mquina en la parte inferior de las prendas.
            .row.px-5.py-4(titulo="Embebido")
              .col-12
                p.mb-0.px-5 Recogido de tela sobrante sin que se formen pliegues, con el fin de que la pieza embebida tenga las mismas dimensiones que la pieza a la que se va a unir.
            .row.px-5.py-4(titulo="Equidistancia")
              .col-12
                p.mb-0.px-5 Igualdad de distancia entre varios puntos u objetos.
            .row.px-5.py-4(titulo="Escalado")
              .col-12
                p.mb-0.px-5 Tcnica o proceso mediante el cual se obtienen las tallas inferiores y superiores a partir de un patrn base, incrementando o disminuyendo las proporciones segn lo consignado en la tabla de tallas y medidas.
            .row.px-5.py-4(titulo="Escote")
              .col-12
                p.mb-0.px-5 Abertura en prendas de vestir superiores, por la que asoma el cuello y parte del pecho o de la espalda.
            .row.px-5.py-4(titulo="Escuadrar")
              .col-12
                p.mb-0.px-5 Formar ngulo recto entre dos lneas o partes del patrn.
            .row.px-5.py-4(titulo="Espalda")
              .col-12
                p.mb-0.px-5 En las prendas superiores la pieza que cubre la espalda del cuerpo humano.
            .row.px-5.py-4(titulo="Espejo, mitad o lomo")
              .col-12
                p.mb-0.px-5 Se refiere al doblez del molde o patrn con el fin lograr simetra, es decir, que el lado derecho sea idntico al izquierdo.
            .row.px-5.py-4(titulo="Falso")
              .col-12
                p.mb-0.px-5 Pieza de refuerzo que se ubica en la parte interna de las prendas dando fuerza o terminacin a los bordes.
            .row.px-5.py-4(titulo="Flojo")
              .col-12
                p.mb-0.px-5 Es una tolerancia o medida de ms que sirve para evitar que la prenda quede ajustada o tirante.
            .row.px-5.py-4(titulo="Fondo de bolsillo")
              .col-12
                p.mb-0.px-5 Bolsa interna de tela en donde se guardan objetos, compuesto por dos fondos o tapas y una vista.
            .row.px-5.py-4(titulo="Industrializacin de patrones")
              .col-12
                p.mb-0.px-5 Marcacin de los patrones con la simbologa y codificacin necesarias para indicar caractersticas y acciones a realizar al momento de preparar y efectuar el corte y confeccin de la prenda.
            .row.px-5.py-4(titulo="Lateral")
              .col-12
                p.mb-0.px-5 El lado o el costado de una pieza.
            .row.px-5.py-4(titulo="Lnea")
              .col-12
                p.mb-0.px-5 Adicional al concepto de lnea en dibujo, en patronaje se refiere al conjunto de prendas de vestir dirigidas a un mercado especfico, por ejemplo: lnea femenina, lnea masculina, lnea infantil y lnea interior, entre otras.
            .row.px-5.py-4(titulo="Manga")
              .col-12
                p.mb-0.px-5 Parte de las prendas superiores que rodea cada uno de los brazos. Existen muchos tipos de mangas corta, ranglan, tres cuartos () y larga, entre otras.
            .row.px-5.py-4(titulo="Pantaln")
              .col-12
                p.mb-0.px-5 Prenda inferior que consta de dos perneras que generalmente, cubren desde la cintura hasta los tobillos.
            .row.px-5.py-4(titulo="Paralela")
              .col-12
                p.mb-0.px-5 Lneas equidistantes entre s y que por ms que se prolonguen no pueden encontrarse.
            .row.px-5.py-4(titulo="Pasador")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen13.jpg")
              .col-lg-7
                p.mb-0.px-5 Pieza delgada ubicada generalmente en la pretina por donde pasa un cinturn.
            .row.px-5.py-4(titulo="Patrn o molde")
              .col-12
                p.mb-0.px-5 Cada una de las piezas o componentes del patrn de la prenda, ya sean dibujadas, cortadas en papel, cartn o sistematizadas, cada uno de ellos puede copiarse, reproducirse en serie o transformarse. El conjunto del total de componentes de la prenda se llama patrn del modelo.
            .row.px-5.py-4(titulo="Patronaje")
              .col-12
                p.mb-0.px-5 Como se expone en el tema anterior, es el procedimiento ordenado para la construccin de los moldes o patrones de una prenda de vestir.
            .row.px-5.py-4(titulo="Patrones base")
              .col-12
                p.mb-0.px-5 Son aquellos que sirven como punto de partida para la creacin de diferentes modelos.
            .row.px-5.py-4(titulo="Perilla")
              .col-12
                p.mb-0.px-5 Pieza que complementa y da terminacin a la abertura frontal de la camisa o de la manga.
            .row.px-5.py-4(titulo="Permetro")
              .col-12
                p.mb-0.px-5 Conjunto de lneas que limitan una figura o una superficie, es el contorno de una figura.
            .row.px-5.py-4(titulo="Pernera")
              .col-12
                p.mb-0.px-5 Cada una de las partes del pantaln que cubre cada pierna. Pernera delantera, pieza que cubre la parte delantera de la pierna, pernera posterior pieza opuesta y el complemento de la pernera delantera. Se hace necesario partir del molde bsico de la pernera delantera para trazar el molde de la posterior.
            .row.px-5.py-4(titulo="Perpendicular")
              .col-12
                p.mb-0.px-5 Recta o plano que forma ngulo recto (de 90) con otra recta o plano, en patronaje tambin conocido como escuadrar.
            .row.px-5.py-4(titulo="Pieza")
              .col-12
                p.mb-0.px-5 Molde o patrn de cada una de las partes que, en conjunto, componen una prenda.
            .row.px-5.py-4(titulo="Pinza")
              .col-12
                p.mb-0.px-5 Pequeo pliegue cosido que termina en punta, que permite una mejor adaptacin de la prenda a las formas del cuerpo (caderas, pecho, entre otras). La pinza se dibuja abierta en los patrones bases, siendo el largo de pinza la altura de la misma, y la profundidad de pinza la anchura.
            .row.px-5.py-4(titulo="Piquete")
              .col-12
                p.mb-0.px-5 Seal, corte o muesca que se marca en el borde de un patrn para delimitar puntos importantes, facilitar el montaje y costura de una prenda.
            .row.px-5.py-4(titulo="Pretina")
              .col-12
                p.mb-0.px-5 Parte de las prendas inferiores que se ajusta a la cintura.
            .row.px-5.py-4(titulo="Rectngulo")
              .col-12
                p.mb-0.px-5 Figura geomtrica (cuadriltero) que tiene cuatro ngulos rectos (90) y cuyos lados son iguales de dos a dos.
            .row.px-5.py-4(titulo="Ruedo")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen14.jpg")
              .col-lg-7
                p.mb-0.px-5 Tambin conocido como bajo, es el orillo o borde inferior que da el acabado a la parte inferior de las prendas.
            .row.px-5.py-4(titulo="Sesgo o Bies")
              .col-12
                p.mb-0.px-5 Accin de trazar la lnea de aplome a un molde en diagonal, con el fin de mejorar la cada de la tela en la pieza, se utiliza en drapeados o prendas con mucho vuelo.
            .row.px-5.py-4(titulo="Sisa")
              .col-12
                p.mb-0.px-5 Abertura o corte curvo en las prendas superiores, que permite el paso del brazo y se une a las mangas (en caso de llevarlas).
            .row.px-5.py-4(titulo="Talla")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen15.jpg")
              .col-lg-7
                p.mb-0.px-5 Estandarizacin de medidas que permite clasificar a las personas segn su tamao e identificarlas para el uso de las prendas de vestir, nombradas alfanumricamente segn el tipo de prenda o la lnea de vestuario, se utilizan para la elaboracin de un molde, patrn base o bsico.
            .row.px-5.py-4(titulo="Tela")
              .col-lg-5.mb-lg-0.mb-5
                figure
                  img(src="@/assets/curso/tema1-imagen16.jpg")
              .col-lg-7
                p.mb-0.px-5 Material generalmente constituido por muchos hilos entrecruzados que forman una especie de hoja o lmina flexible, utilizado para la confeccin de prendas de vestir, entre otros usos.
            .row.px-5.py-4(titulo="Tiro")
              .col-12
                p.mb-0.px-5 En las prendas inferiores la distancia desde la unin de las perneras y la cintura.
            .row.px-5.py-4(titulo="Trazar")
              .col-12
                p.mb-0.px-5 Hacer trazos o lneas, tambin ejecutar la accin del trazado.
            .row.px-5.py-4(titulo="Trazo")
              .col-12
                p.mb-0.px-5 Lnea o raya que constituye la forma o el contorno de algo.
            .row.px-5.py-4(titulo="Trazo o trazado industrial")
              .col-12
                p.mb-0.px-5 Organizar y marcar las piezas que componen el diseo o modelo, de manera ptima y eficiente segn los materiales para hacer el corte.
            .row.px-5.py-4(titulo="Vrtice")
              .col-12
                p.mb-0.px-5 Interseccin de dos lneas que forman un ngulo.
            .row.px-5.py-4(titulo="Vista")
              .col-12
                p.mb-0.px-5 Como su nombre lo indica, pieza del interior de la prenda que queda visible y que parte del mismo patrn inicial, se utiliza para formar la solapa en determinados modelos, para rematar y dar un buen acabado. Pieza que va sobrepuesta en el fondo de bolsillo y se corta en la misma tela del pantaln, ayudando a ocultar el forro.
            .row.px-5.py-4(titulo="Vivo")
              .col-12
                p.mb-0.px-5 Franja de tejido o tela cortada al bies que se coloca para rematar la prenda, sustituyendo a la vista o el ruedo, tambin utilizadas para rematar aberturas y formar as bolsillos y ojales.

    p.mb-0 En el ejercicio profesional se hace fundamental emplear adecuadamente los trminos, al igual que mantener actualizado el lxico segn los avances de las tcnicas y tecnologas.
