                  p.mb-0 Se utiliza para crear marcas en los bordes de los patrones, que sirven como piquetes, costuras, dobladillos, seas para identificar frente de espalda en los patrones, entre otros usos (Isnt that sew, s.f.).
                .col-lg-5
