    #t_1_4.titulo-segundo
      h2 1.4   Fundamentos de antropometra y ergonoma

    p.mb-5 El proceso de patronaje comienza entendiendo el cuerpo humano, su forma, dimensiones (medidas), proporciones y capacidades motrices (movimiento). Para conseguirlo se hace indispensable que un patronista tenga conocimientos en antropometra y en ergonoma del vestir.

    .row.justify-content-center
      .col-lg-7
        figure
          img(src="@/assets/curso/tema1-imagen37.jpg")
        .cajon.color-primario.p-5.mb-4
            p.mb-0 Para comenzar a entender el patronaje de prendas de vestir, es importante entender que el cuerpo humano puede compararse con una figura geomtrica tridimensional (3D) con forma cilndrica, que al abrirla para llevarla a una figura plana (bidimensional  2D) se obtiene un rectngulo que representa el rea que cubre el cuerpo humano, como se puede ver en las siguientes imgenes:
        figure.mb-5
          img(src="@/assets/curso/tema1-imagen38.svg")
          figcaption Fuente: Tcnicas de patronaje. Tomo I: Mujer (Carrera de Diseo y Gestin en Moda, 2015a).
        p.mb-5 Por lo anterior es que, al momento de medir el cuerpo, se toman medidas de contornos (perimetrales), longitudinales verticales (largos) y longitudinales horizontales (anchos), que luego se traducirn sobre el patrn. Con el fin de mantener la simetra en los moldes, las medidas de contorno se trabajan por cuartos (1/4) con eje en el centro frente y el centro espalda.
          br
          br
          | La antropometra, es un trmino proveniente del griego anthropos (hombre) y metrikos (medida), es el estudio del tamao, proporcin, forma y composicin del cuerpo humano. Se basa en las medidas corporales, el somatotipo, el estudio de la proporcionalidad y de la composicin corporal.
        p.mb-5.border-bottom.text-center Existen algunos trminos de la antropometra que se emplean en la moda, tales como:

      .col-lg-11.mb-5
        .row.justify-content-center.mb-5
          .col-lg-4.mb-lg-0.mb-5
            .tarjeta-avatar
              img(src='@/assets/curso/tema1-imagen39.svg' alt='AvatarTop')
              .tarjeta.color-primario
                .p-5
                  h4.text-center Arquetipo
                  p.mb-0 El cual se refiere al cuerpo ideal o perfecto (sin embargo, pocos individuos tienen estas caractersticas corporales).

          .col-lg-4.mb-lg-0.mb-5
            .tarjeta-avatar
              img(src='@/assets/curso/tema1-imagen40.svg' alt='AvatarTop')
              .tarjeta.color-primario
                .p-5
                  h4.text-center Estereotipo
                  p.mb-0 Que es la agrupacin de personas con caractersticas similares, de estos grupos se extraen la mayora de cuadros de tallas y medidas.

          .col-lg-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema1-imagen41.svg' alt='AvatarTop')
              .tarjeta.color-primario
                .p-5
                  h4.text-center Somatotipos o tipo fsico
                  p.mb-0 Se utiliza para estimar la forma corporal y su composicin, obteniendo un anlisis del fsico de cada individuo.
        .row.justify-content-center
          .col-lg-7.mb-lg-0.mb-5
            p.mb-2 Si bien cada individuo tiene caractersticas nicas y la forma de su cuerpo lo identifica, en la moda, para el desarrollo de las prendas se hace necesario agrupar las formas y medidas corporales de acuerdo con el tipo de usuario o perfil de consumidor al que se dirige el producto.
              br
              br
              | Para lo cual se hacen estudios de medidas de grupos de personas, teniendo en cuenta sus condiciones y estilo de vida, es decir, edad, sexo bilgico (femenino, masculino), lugar de procedencia, lugar de residencia, clima, tipo de trabajo y tipo de actividades fsicas, entre otras. A los individuos de la muestra se les toman las medidas corporales requeridas, dividiendo entre el nmero de sujetos de la muestra, lo que da lugar a la media, que puede tener valor por si misma o ser comparada con otras medidas.
              br
              br
              | Dando como resultado los cuadros de tallas y medidas, los cuales deben corresponder al perfil del consumidor de la marca.
            img(src="@/assets/curso/tema1-imagen42.svg")
          .col-lg-5
            .indicador__container
              .indicador--click
            figure.hover-image(@click="openClickAnexo(obtenerLink('/downloads/A_U1_Manual_patronaje_basico_interpretacion_diseno.pdf\#page=23'))")
              img(src="@/assets/curso/tema1-imagen43.svg")
      .col-lg-9.mb-5
        .caja.temporal--azul.text-center.py-3
          p.mb-0 En temas posteriores se describir el proceso de toma de medidas corporales para el desarrollo de patrones.
      .col-lg-11.mb-4
        p.mb-5 La ergonoma hace referencia a la adaptacin entre los objetos (mquinas, muebles, utensilios, entre otros) y la persona que los emplea, para lograr una mayor comodidad y eficacia. Para el caso de la moda, la ergonoma del vestuario, es el ajuste de la ropa sobre el cuerpo humano, cubriendo de manera ptima cada una de sus partes y permitiendo la <i>performance</i> de la actividad para la que est diseado cada producto.
