    #t_1_5.titulo-segundo
      h2 1.5   Mrgenes, desahogos y tipos de costuras

    p.mb-5 Una vez realizados los patrones o moldes de las prendas, los cuales vienen sin la adicin de tela necesaria para poder unirlos, se le debe aadir lo que se denomina margen o ventaja de costura. Esta margen depender del tipo de tela o tejido, el tipo de costura o la mquina que se emplear y del modelo que se va a coser.

    .cajon.color-primario.p-5.mb-0
      p.mb-0 Algunos moldes ya tienen incluidas las costuras, esto se sabr cuando en las teoras para su desarrollo as lo indique, sin embargo, a nivel industrial, las mrgenes de costura se aaden una vez se desarrolla el modelo, lo cual quiere decir que los patrones base o moldes bsicos no las incluyen.
    figure.mb-5
      img(src="@/assets/curso/tema1-imagen45.jpg")

    p.mb-5 Como se ha expresado la margen de costura corresponde directamente a la operacin, parte del patrn y a la mquina con que ser confeccionada, es por ello que el patronista debe prestar atencin a los detalles consignados en la ficha tcnica, documento en el cual se rene toda esta informacin.
      br
      br
      | A continuacin, se indicarn algunos ejemplos

