                  span 1
                | La fileteadora puntada sencilla utilizada generalmente en tejidos de punto requiere un margen de entre 0,25 cm a 0,5 cm dependiendo del tipo de prenda y la funcin que va a cumplir.
              li
                .lista-ol--cuadro__vineta.circle
                  span 2
                | La fileteadora con puntada de seguridad (utilizada en tejido plano) requiere de 0,7 cm, 1,2 cm o 1,5 cm; lo cual depende del ajuste de la mquina y el requerimiento de la parte o costura.
              li
                .lista-ol--cuadro__vineta.circle
                  span 3
                | Las cerradoras de codo (empleadas en costados de camisas y pantalones) utilizan entre 1,2 cm y 2,5 cm de margen de costura, esto debido a que la tela se dobla para ser encarterada.
              li
                .lista-ol--cuadro__vineta.circle
                  span 4
                | Para los ruedos o bajos depender del procedimiento que se realice, es decir, si se har sencillo en una recubridora, slo habr que dar la ventaja deseada en el ruedo (Ej. 2,5 cm), mientras que si ste se har dobladillado (doblado y vuelto a doblar) en plana se deber adicionar la ventaja del ruedo ms la del doblez (Ej. 2,5 cm + 1 cm de doblez).
          .col-lg-12
            p.text-center.mb-4.fw-bold Cuando no se cuenta con la informacin de las costuras cunto margen de costura aadir? Hay una medida universal?
            ol.lista-ol--cuadro
              li
