        div.additional-ext.pt-1
          .row.justify-content-center.mt-5
            .col-lg-8
              p.mb-4 La palabra desahogo es el aumento, amplitud o soltura que se debe aadir al patrn de la prenda de vestir, para que, aparte de la comodidad facilite su postura o el movimiento que se haga con ella (<i>performance</i>).
              figure.mb-5
                img(src="@/assets/curso/tema1-imagen56.jpg")
              p.mb-4 Los desahogos u holguras se dan en centmetros o pulgadas segn el sistema mtrico que se utilice en la empresa a trabajar. Dependen directamente del tejido y el desarrollo del modelo a realizar, se aaden luego de tener el patrn listo, tanto por contornos, como en los largos.
              .titulo-sexto.color-acento-contenido
