            p.mb-4 Es importante establecer el tipo de prenda a la que se le aadirn, si es interior o exterior, la lnea a la que se dirige (femenino, masculino, infantil), el tejido que se utilizar y los puntos en que se ajustar o sujetar en el cuerpo humano. Las prendas se pueden clasificar en tres pieles o capas as:
