            p.mb-0 Segn el tipo de pas en el que se viva y su clima, pueden existir ms capas de vestir o pieles. Cada una de las capas tendr mayor capacidad o desahogo que la anterior, con el fin de poder cubrir cmodamente el cuerpo.
