        p.mb-5 A las prendas de vestir que se desarrollan en tejido de punto con elongacin y son ajustadas sobre el cuerpo (Ej. interior, active) no se les aplica desahogo, por el contrario, se aplica una reduccin de medida, conocida como porcentaje de elongacin, el cual depender de cun elstico sea el material textil, a continuacin, se presenta un ejemplo de aplicacin de la frmula de porcentaje de elongacin para un material con 25%.
