          p.mb-0  La holgura es una amplitud determinada a veces como espacio independiente a los desahogos. Estos valores se trabajan de forma estndar, aunque algunas marcas de ropa comercial y diseadores famosos han hecho variantes apropindose de otros nombres y estilos.
