        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
          .row(titulo="Silueta entallada  <i>Slim fit</i>  ")
            .col-lg-6.mb-lg-0.mb-5
              p.mb-0 Es la predominante en diseos confeccionados en telas elsticas.  Normalmente son prendas ajustadas al cuerpo. No necesitan holgura. Estas prendas se adaptan a la figura. Moldean el busto, la cadera, la cintura. Su holgura es de 0 cm.
            .col-lg-6
              figure
                img(src="@/assets/curso/tema1-imagen59.jpg")
          .row(titulo="Silueta semientallada  <i>Straight fit</i> ")
            .col-lg-6.mb-lg-0.mb-5
              p.mb-0 Es predominante en diseos confeccionados en telas semielsticas y telas livianas. Tambin en algunas telas rgidas.  Normalmente son prendas casi pegadas al cuerpo, con algo de movilidad. Siguen la lnea del cuerpo, con una holgura total de 8 cm. Es decir, hacer el trazo del patrn normal, pero con las medidas de contorno aumentadas en 8 cm a 10 cm.
            .col-lg-6
              figure
                img(src="@/assets/curso/tema1-imagen60.jpg")
          .row(titulo="Silueta normal  <i>Regular fit</i>")
            .col-lg-6.mb-lg-0.mb-5
              p.mb-0 Es predominante en diseos confeccionados en telas semielsticas y telas livianas. Tambin en algunas telas rgidas.  Normalmente son prendas casi pegadas al cuerpo, con algo de movilidad. Siguen la lnea del cuerpo, con una holgura total de 8 cm. Es decir, hacer el trazo del patrn normal, pero con las medidas de contorno aumentadas en 8 cm a 10 cm.
            .col-lg-6
              figure
                img(src="@/assets/curso/tema1-imagen61.jpg")
          .row(titulo="Silueta amplia  <i>Large fit</i>")
            .col-lg-6.mb-lg-0.mb-5
              p.mb-5 Esta silueta se usa para dar mucho vuelo y movilidad a los diseos. Se usan telas rgidas tanto livianas, medianas como pesadas.  Son prendas muy sueltas con gran movilidad. Se caracteriza por tener sisas muy grandes, no se marca la cintura, muchos fruncidos. Este tipo de prendas tienen holgura hasta de 20 cms en los contornos.
