    #t_1_6.titulo-segundo
      h2 1.6   Toma de medidas antropomtricas

    p.mb-5 La toma de medidas antropomtrica es el comienzo del proceso de patronaje, como se ha expresado con anterioridad. Es fundamental tener las medidas, puesto que a partir de stas se desarrollan los patrones para adecuarse a los volmenes y formas del cuerpo a vestir.

    .row.justify-content-center.mb-5
      .col-lg-5
        figure
          img(src="@/assets/curso/tema1-imagen64.jpg")
      .col-lg-7
        p.mb-4 Para la toma de medidas se debe contar con unas condiciones adecuadas del espacio y unos equipos y herramientas apropiados. El espacio deber ser agradable y propiciar privacidad, para que as la persona tenga la disposicin voluntaria de participar y se sienta en confianza.
        .cajon.color-acento-contenido.p-5.mb-4
          p.mb-0  Disponer del espacio suficiente en el que estarn el sujeto a medir, el antropmetra (persona que toma las medidas) y el anotador (persona que escribe las medidas).
        p.mb-0 La persona a medir deber estar en ropa interior o ropa ligera (el traje de bao de dos piezas sin rellenos, o trusa, es el ideal para mujeres), no debe portar calzado ni medias, el cabello recogido (en caso de tenerlo largo). Asumir una postura erguida de pie, con la cabeza al frente y los ojos mirando a un punto fijo, las extremidades superiores relajadas a los lados del cuerpo, con los dedos extendidos apuntando hacia abajo (la postura de las extremidades superiores podr variar segn la medida a tomar), los pies levemente separados. Se mantendr en estado de relajacin, con la respiracin reposada y relajada, ya que puede afectar las medidas del trax.

    .row.justify-content-center.mb-5
      .col-lg-9
        p.text-center.caja.temporal--rosa.p-4 Se emplean diferentes o herramientas de medicin dependiendo medida a tomar, as:
      .col-lg-10
        PasosA.color-acento-contenido.mb-5(tipo="n")
          .row
            .col-md-5.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/tema1-imagen65.jpg', alt='Texto que describa la imagen')
            .col-md-7
              h3 Bscula digital
              p Se utiliza para determinar el peso corporal, generalmente expresado en kilogramos.

          .row
            .col-md-7.mb-4.mb-md-0
              h3 Cinta mtrica o antropomtrica
              p Debe ser flexible sin llegar a elstica, con una anchura inferior a 0.7 cm y un espacio regulador antes del cero, de al menos 4 cm, con las medidas en centmetros y milmetros marcadas claramente y de fcil lectura.
            .col-md-5
              figure
                img(src='@/assets/curso/tema1-imagen66.jpg', alt='Texto que describa la imagen')

          .row
            .col-md-5.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/tema1-imagen67.jpg', alt='Texto que describa la imagen')
            .col-md-7
              h3 Tallmetro
              p Instrumento utilizado para tomar la estatura con una precisin de 1 mm. En caso de no contar con tallmetro se puede emplear una cinta mtrica invertida, comenzando por el cero (0) en la base o piso y con la ayuda de una escuadra tomar la medida.

          .row
            .col-md-7.mb-4.mb-md-0
              h3 Lpices dermogrficos:
              p Se utilizan para marcar sobre el cuerpo los puntos de referencia para la toma de medidas, stos pueden ser reemplazados por lpices de maquillaje para ojos o cejas.
            .col-md-5
              figure
                img(src='@/assets/curso/tema1-imagen68.jpg', alt='Texto que describa la imagen')
