                      |   permetro situado en el punto medio entre el pliegue inguinal y la parte superior de la rtula (rodilla). Generalmente se ubica a 15 cm de la parte superior de la rtula.
                .row.justify-content-center.align-items-center
                  .col-lg-5.d-flex
                    figure
