                      figcaption Fuente: Grupo de Investigacin INAMOD (Gaviria et al., 2019).
                  .col-lg-7.d-flex
                    p.mb-0
                      strong Contorno de rodilla:
                      |   permetro mximo de la rodilla, a nivel de la rtula.
            .row.justify-content-center
              .col-md-10
                .row.justify-content-center.align-items-center
                  .col-lg-5.d-flex
                    figure
                      img(src="@/assets/curso/tema1-imagen100.jpg")
                  .col-lg-7.d-flex
                    p.mb-0
                      strong Contorno de pantorrilla:
                      |   Contorno de pantorrilla: permetro mximo de la pantorrilla a nivel de la marca del pliegue de Pantorrilla medial.
                .row.justify-content-center.align-items-center
                  .col-lg-5.d-flex
                    figure
                      img(src="@/assets/curso/tema1-imagen101.jpg")
                      figcaption Fuente: Grupo de Investigacin INAMOD (Gaviria et al., 2019).
                  .col-lg-7.d-flex
                    p.mb-0
                      strong Contorno de tobillo:
                      |   permetro del tobillo es tomado en el punto ms estrecho por encima del malolo tibial lateral y medial, considerado el menor permetro de la parte inferior de la pantorrilla.
        .row.justify-content-center.align-items-center.background1-especial2.border-radius.left.right.mb-5
          .col-auto.background1-especial1.border-radius.right
            figure.p-4
              img(src="@/assets/curso/tema1-imagen63.svg")
          .col
            p.mb-0 Nota: las medidas deben ser tomadas sobre la superficie sin presionar o contraer el cuerpo con la cinta mtrica y sin incluir el dedo del antropmetra.

        figure
          img(src="@/assets/curso/tema1-imagen102.jpg")
      .col-lg-10
        p.mb-5 Cuando se trabajan diseos personalizados, se le toman las medidas a cada uno de los clientes a vestir. Al tratarse de patronaje industrial, se deben tomar medidas a una muestra significativa de la poblacin y estandarizar tallas de acuerdo a los resultados del estudio, esto es lo que se conoce como cuadro de tallas. Cada empresa trabaja con su propio cuadro de tallas, puesto que su consumidor se diferencia de las dems. Existen normas tcnicas internacionales para la regulacin del tallaje y medidas de los consumidores, sin embargo, siempre se recomienda hacer el propio estudio de medidas y tallas.

        .row.justify-content-center
