    #t_1_7.titulo-segundo
      h2 1.7   Patronaje industrial manual y sistematizado

    p.mb-5 El patronaje industrial es el que se realiza en serie, para la produccin de prendas de vestir a niveles de industria, para marcas o empresas mayoristas, se diferencia del patronaje personalizado en el uso de tallas estandarizadas, el escalado y el trazado de lotes. Este proceso tradicionalmente se ha realizado de manera manual, gracias a los avances de la tecnologa existen <i>softwares</i> que permiten realizarlo de manera sistematizada, lo que le proporciona precisin y agilidad a la labor.

    figure
      img(src="@/assets/curso/tema1-imagen104.jpg")

    p.mb-5 Como su nombre lo indica, el patronaje manual, es aquel que se realiza con las manos, utilizando herramientas bsicas para trazar los patrones o moldes sobre un papel, escalarlos, desarrollarlos y recortarlos, este mtodo requiere mayor tiempo y dedicacin por parte del patronista. El consumo de tela en ocasiones se ve afectado (mayor cantidad de tela requerida) ya que no se cuenta con la exactitud brindada por el <i>software</i> especializado.

    .row.justify-content-center
      .col-lg-10
        .cajon.color-acento-contenido.p-5.mb-4
          .row.justify-content-center
            .col-lg-8.mb-lg-0.mb-5
              p.mb-0 Para realizar moldes sistematizados, es decir con la ayuda de un <i>software</i> (programa de computador), se hace indispensable saber patronar de manera manual. Las ventajas de este sistema son el ahorro de espacio fsico para guardar patrones bsicos y desarrollos en las diferentes tallas, la agilidad para acceder y modificar los patrones, al igual que la facilidad y precisin para el escalado. El trazado (mdulo generalmente independiente) es quizs el proceso en el que ms se evidencia la velocidad y exactitud para acomodar las diferentes piezas en las tallas a elaborar. Adicionalmente los <i>softwares</i> de patronaje han ido incorporando la posibilidad de realizar prototipos en 3D, lo cual le permite al patronista validar el producto en tiempo real y ahorrar en procesos de trazo, corte y confeccin de muestras preliminares, y observar en el mapa de tensiones la vestibilidad de la prenda.
            .col-lg-4
              figure
                img(src="@/assets/curso/tema1-imagen105.svg")
        p.mb-5 Los programas de Diseo Asistido por Computador (CAD), trabajan de manera similar utilizando en el rea de trabajo las coordenadas del plano cartesiano para determinar los puntos de construccin de los patrones. Los ms utilizados a nivel comercial en Colombia, son:
        SlyderC.mb-5(:datos="datosSlyder2")
      .col-lg-8
        .cajon.color-primario.p-5.mb-4
          p.mb-0 Los anteriores <i>softwares</i> requieren una membresa y trabajan con un sistema de hardware (llave) licenciado, las empresas adquieren el nmero de licencias y mdulos segn el nmero de puestos de trabajo (patronistas, trazadores y cortadores, entre otros). Existen algunos <i>softwares</i> de uso libre, tales como:
        .row.justify-content-center.mb-5
          .col-lg-6.mb-lg-0.mb-5
            figure
              img(src="@/assets/curso/tema1-imagen111.jpg")
            p.text-center
              strong Valentina
              br
              | https://valentinaproject.bitbucket.io/
          .col-lg-6
            figure
              img(src="@/assets/curso/tema1-imagen112.jpg")
            p.text-center
              strong Seamly2D
              br
              | https://seamly.net/
        p.mb-5 Al igual que todas las actividades humanas, el patronaje requiere de prctica y dedicacin para conseguir la experticia en las diferentes lneas de vestuario y con diversos materiales.
          br
          | (Innovacin y Cualificacin, 2018; Lafayette, 2019)


