    #t_1_8.titulo-segundo
      h2 1.8   Sealizacin e industrializacin de patrones

    p.mb-5 La sealizacin e industrializacin de los patrones, es la fase del proceso en la que se realiza la identificacin, codificacin y registro de algunas indicaciones para el corte de la moldera. Esto se logra a partir del uso de una serie de smbolos y convenciones en cada pieza del patrn que brindan informacin sobre la forma en la que se debe ubicar el molde en la tela, nmero de piezas por patrn, nmero de veces que debe ser cortada la pieza, los materiales en los que debe ir la pieza, muescas o piquetes a realizar.

    .row.justify-content-center
      .col-lg-8
        figure.mb-5
          img(src="@/assets/curso/tema1-imagen113.svg")
          figcaption Fuente: Trazo, corte y confeccin de la falda clsica (SENA, 2014).
        p.mb-5 Adems de estas convenciones, en el proceso de industrializacin de la moldera se incluye la informacin relacionada con la clasificacin de la prenda, es decir, nmero de la referencia, nombre del diseo, referencia, pieza a la que corresponde el patrn, toda la informacin que facilite su codificacin y gil interpretacin del patrn.
        figure.mb-5
          img(src="@/assets/curso/tema1-imagen114.svg")
          figcaption Fuente: Trazo, corte y confeccin de la falda clsica (SENA, 2014).
        p.mb-5 De esta manera, la sealizacin e industrializacin permiten al personal patronista hacer una interpretacin del desarrollo del patrn, adems de orientaciones al proceso de corte y confeccin.
        figure.mb-5
          img(src="@/assets/curso/tema1-imagen115.svg")
          figcaption Fuente: SENA, 2014.

      .col-lg-6.mb-lg-0.mb-5
