    #t_1_9.titulo-segundo
      h2 1.9   Encogimientos y acabados

    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-5
        p.mb-0 Para poder desarrollar patrones, es necesario tener ciertos conocimientos bsicos sobre la materia prima, en este caso textiles (telas), pues su comportamiento y caractersticas influyen en la construccin del patrn. Tanto los acabados como los encogimientos son caractersticas de los textiles que pueden significar una modificacin en el aspecto, textura o dimensin de la tela.
          br
          br
          | Los acabados son procesos que se le realizan a la tela para modificar su apariencia en trminos de textura, sensacin o comportamiento. Pueden ser transformaciones superficiales como algunos mtodos de estampacin, o cambios en la forma de construccin del tejido. Un acabado puede hacer que la tela sea repelente al agua, tenga un proceso de combustin lenta o antiarrugas, entre otros.
      .col-lg-4
        figure
          img(src="@/assets/curso/tema1-imagen116.jpg")
    .row.justify-content-center
      .col-lg-10.mb-lg-0.mb-5
        .bloque-texto-c.color-secundario.p-4.mb-5
          i.fas.fa-quote-right
          h4.mb-2 La importancia de conocer sobre acabados radica principalmente en dos razones: la primera es poder hacer seleccin de materiales dependiendo de la funcionalidad que se espera de la prenda, para determinar aspectos como la cada (suficiencia), control de fluidos y conservacin de temperatura, entre otros. La segunda razn es lograr resaltar formas del cuerpo con el uso de materiales, es decir, las telas voluminosas o gruesas agregaran visualmente rigidez y tamao al cuerpo que se viste.
          span -  Priest &amp; Pullen, 2001.
        p.mb-5 Los acabados se pueden clasificar en dos grandes grupos, los fsicos y los qumicos. Los acabados fsicos son aquellos en los que, mediante el uso del calor, se modifica el tacto de la tela mediante la alteracin de su estructura superficial, es decir, hay una modificacin de la textura como lo son los grabados. Estos pueden ser sobre el tejido seco o hmedo.
        .tarjeta.tarjeta--gris.p-4.mb-5
          LineaTiempoC.color-acento-contenido(text-small)
            .row.justify-content-center(titulo="Parte 1")
              .col-lg-4.mb-lg-0.mb-5
                figure.mb-4
                  img(src="@/assets/curso/tema1-imagen117.jpg")
                  figcaption Fuente: tintoreriaindustrial.com (s.f.)
                figure.mb-4
                  img(src="@/assets/curso/tema1-imagen118.jpg")
                  figcaption Fuente: annamcneary.com (s.f.).
              .col-lg-8
                p.mb-4.caja.temporal--azul.p-3.text-center Dentro de los acabados secos
                  br
                  | (sin presencia de vapor o agua) estn:
                p.mb-5
                  strong Calandrado:
                  |    por medio de la friccin, presin y calor se obtiene un aspecto liso, brillante y compacto. Las fibras que sobresalen del tejido  y que son las causantes de la opacidad- son plegadas obteniendo mayor lisura y por ende aumentando su reflectancia, lo que da su apariencia de brillo. Es un acabado pasajero que disminuye con el lavado.
                p.mb-5
                  strong Gofrado:
                  |    tambin conocido como grabado. Es el proceso en donde un cilindro metlico tallado o grabado ejerce friccin y calor sobre la tela, como en el calandrado y transfiere el diseo a la tela. El efecto puede ser permanente mediante el uso de sustancias tipo resinas que termofijan (fijacin por medio de calor) el material.
            .row.justify-content-center(titulo="Parte 2")
              .col-12
                .row.justify-content-center.mb-4
                  .col-lg-4.mb-lg-0.mb-5
                    figure.mb-0
                      img(src="@/assets/curso/tema1-imagen119.jpg")
                      figcaption Fuente: experimentaciontextilartesanal. wordpress.com (2016).
                  .col-lg-8
                    p.mb-0
                      strong Esmerilado (lijado o arenado):
                      |   da la apariencia de textura similar a la piel de durazno. La tela pasa por una maquina con varios rodillos que contienen un material abrasivo desgastando superficialmente el tejido. Este proceso se puede realizar de manera manual o artesanal con el uso de lijas de diferentes calibres.
                      br
                      br
                      strong Cepillado:
                      |   se utiliza para limpiar impurezas, levantar o acostar el pelo de loes tejidos perchados o hacer un esmerilado ligero.
                .row.justify-content-center
                  .col-lg-8.mb-lg-0.mb-5
                    p.mb-0
                      strong Perchado:
                      |   este acabado genera un efecto aislante y aumenta la cobertura del material dando la sensacin de ser ms abrigadora aumentando su suavidad por unas de las caras de la tela.
                  .col-lg-4
                    figure.mb-0
                      img(src="@/assets/curso/tema1-imagen120.jpg")
                      figcaption Fuente: pe.all.biz/acabado-textil (s.f.).
            .row.justify-content-center(titulo="Parte 3")
              .col-12
                p.caja.temporal--azul.mb-3.px-5.py-3 Los acabados en hmedo tienen como caracterstica la presencia de agua  ya sea lquida o en vapor- acelerando el proceso de arreglo o moldeo de las fibras segn el acabado que se busque. Por lo general son utilizados con el fin de estabilizar las dimensiones de la tela por el tema del encogimiento del material.
                .row.justify-content-center
                  .col-lg-4.mb-lg-0.mb-5
                    figure
                      img(src="@/assets/curso/tema1-imagen121.jpg")
                      figcaption Fuente: tintoreriaindustrial.com (s.f.)
                  .col-lg-8
                    p.mb-0
                      strong Sanforizado:
                      |   lo que se pretende con este acabado es estabilizar las dimensiones aplicando vapor y fuerza, lo que produce un encogimiento comprensivo del material. La tela es sometida a tensin sobre una plancha o bandeja de goma, mientras se aplica vapor.
                      br
                      br
                      strong Compactado:
                      |   es un proceso similar al sanforizado, la diferencia es que se usa en tejidos de algodn 100% para garantizar al mximo su estabilidad dimensional durante todo el proceso de lavandera y teido en los cuales por la fuerza de tensin ejercida sobre el material tiende a estirarse, esto es lo que pasa en ocasiones cuando las prendas son lavadas de manera domstica y se encogen
            .row.justify-content-center(titulo="Parte 4")
              .col-lg-4
                figure
                  img(src="@/assets/curso/tema1-imagen122.jpg")
              .col-lg-8
                p.mb-0
                  strong Batanado:
                  |   con este acabado, mediante la aplicacin de humedad combinada con calor, friccin y compresin, se pretende fieltrar los tejidos de lana, stos pierden dimensiones, aumentan su espesor, peso por metro cuadrado y ofrecen ms resistencia a la penetracin.
                  br
                  | El batanado se basa en que la superficie de la fibra de lana est formada por escamas que se acoplan unas con otras al producir un movimiento relativo entre ellas. A pesar de que el dimetro de la fibra no cambia, el aire entre las fibras se elimina a medida que stas se enredan, de modo que el tejido "se encoge".
                  br
                  br
                  strong Decatizado:
                  |   similar al sanforizado, pero para tejidos de calada de lana y sus mezclas. Se adquiere importantes cualidades bsicas como buena cada, resistencia a las arrugas, moderado lustre y estabilidad dimensional ptima  en longitud y anchura  gracias a la aplicacin de vapor a presin.
            .row.justify-content-center(titulo="Parte 5")
              .col-lg-4
                figure
                  img(src="@/assets/curso/tema1-imagen123.jpg")
              .col-lg-8
                p.mb-0
                  strong Vaporizado:
                  |   el vaporizado sin tensin en tejidos de lana es la tcnica ms utilizada para obtener una buena estabilidad dimensional al planchado a vapor.
                  br
                  | La accin de vapor implica el hinchamiento higroscpico de las fibras con una subsiguiente relajacin o contraccin de la tela, que recupera su forma natural. El vaporizado elimina tambin todas las tensiones residuales.
                  br
                  br
                  strong Acabados qumicos:
                  |   mediante la aplicacin de productos o aditamentos qumicos, un tejido puede obtener ciertas caractersticas dndoles un valor agregado pensando en su funcionalidad.
                  br
                  br
                  strong Suavizado:
                  |   a partir del uso de un agente qumico se contribuye a aumentar la suavidad de la tela al penetrar el tejido facilitando el deslizamiento de la fibra.
                  br
                  br
                  strong Antiarrugas:
                  |   tratamiento que se aplica a la tela para reducir el nivel de arrugas e incluso obtener un planchado permanente.
            .row.justify-content-center(titulo="Parte 6")
              .col-lg-8
                p.mb-0
                  strong Antimicrobianos:
                  |   con este tipo de acabado se busca prevenir la transmisin y propagacin de microorganismos y reducir los olores desagradables (desodorizacin). El agente antimicrobiano funciona ya sea por una lenta liberacin del ingrediente activo. O por contacto de la superficie con los microbios.  En la prctica el efecto antimicrobiano se obtiene mediante la aplicacin de productos qumicos especficos durante la fase de acabado o en el proceso de hilatura.
                  br
                  br
                  strong Fungicidas:
                  |   evitan la formacin de hongos sobre la fibra, en tejidos que estn a la intemperie en contacto con la humedad o con el suelo, por ejemplo, lonas, redes, toldos, ropa interior. Se logra mediante la modificacin qumica de las fibras, su impermeabilizacin o la aplicacin de un recubrimiento.
                  br
                  br
                  strong Antispticos:
                  |   tiene por objetivo destruir la accin perjudicial que producen ciertos organismos sobre las fibras textiles. Para eso se aaden al tejido ciertos productos que pueden inhibir la accin del microorganismo o destruirlo.
              .col-lg-4
                figure
                  img(src="@/assets/curso/tema1-imagen124.jpg")
            .row.justify-content-center(titulo="Parte 7")
              .col-12
                .row.justify-content-center
                  .col-lg-8
                    figure
                      img(src="@/assets/curso/tema1-imagen125.jpg")
                      figcaption Fuente: la industria textil y su control de calidad (Lockun; 2012).
                  .col-lg-4
                    p.mb-0
                      strong  Transporte de la humedad (<i>wicking</i>):
                      br
                      |   como su nombre lo indica permite que la humedad y el calor del cuerpo escapen fcilmente. Ayuda a transportar el vapor de agua fuera del cuerpo haciendo que se sienta comodidad y frescura; ayudando a evitar la frialdad del postejercicio.
                .row.justify-content-center
                  .col-lg-8
                    figure
                      img(src="@/assets/curso/tema1-imagen126.jpg")
                      figcaption Fuente: la industria textil y su control de calidad (Lockun; 2012).
                  .col-lg-4
                    p.mb-0
                      strong Repelencia al agua o acabado hidrfugo:
                      br
                      |   permite el paso del aire, pero no del agua. (Lockun, 2012)
            .row.justify-content-center(titulo="Parte 8")
              .col-12
                .row.justify-content-center.mb-5
                  .col-lg-4
                    figure
                      img(src="@/assets/curso/tema1-imagen127.jpg")
                      figcaption Fuente: la industria textil y su control de calidad. (Lockun, 2012).
                  .col-lg-8
                    p.mb-0
                      strong Repelencia y/o liberacin de manchas:
                      |   los tejidos por su naturaleza, superficie irregular y esponjosidad son propensos a almacenar diferentes tipos de suciedad. Este tipo de acabados busca repeler o liberar el tejido de las manchas y suciedades. Dependiendo del tipo de suciedad que se quiera evitar, existen varios tipos de acabado:
                    ul.lista-ul--color
                      li
                        i.lista-ul__vineta
                        | Antiestticos: los tejidos electrostticamente atraen el polvo, que desmejora el aspecto del tejido. Los antiestticos son productos higroscpicos, que captan humedad y eliminan las cargas elctricas de las fibras.
                ul.lista-ul--color
                  li
                    i.lista-ul__vineta
                    | Repelentes a la impureza seca: se tratan los tejidos con xidos metlicos que rellenan las cavidades de las fibras impidiendo que las impurezas se depositen all, dando al tejido un color grisceo.
                  li
                    i.lista-ul__vineta
                    | Repelentes a la suciedad hmeda (anti soil): los repelentes a las manchas se usan en una variedad de telas de algodn, para prendas y hasta tapicera. La gran ventaja es que los tejidos resisten las manchas durante el uso. Cuando ocurre un derrame, esa zona puede limpiarse fcilmente, ya que la mancha se limita a la superficie y no penetra profundamente en el tejido.
                  li
                    i.lista-ul__vineta
                    | Repelentes a las grasas, aceites y bencinas.
            .row.justify-content-center(titulo="Parte 9")
              .col-lg-4
                figure
                  img(src="@/assets/curso/tema1-imagen128.jpg")
                  figcaption Fuente: blog.aznartextil.com/devore/; (s.f.)
                figure
                  img(src="@/assets/curso/tema1-imagen129.jpg")
                  figcaption Fuente: blog.aznartextil.com/devore/; (s.f.)
              .col-lg-8
                p.mb-5.caja.temporal--rosa.py-2.px-5.text-center Acabados cidos
                p.mb-0
                  strong Apergaminado:
                  |   en las telas de algodn, los efectos translcidos se producen mediante un tratamiento con cido sulfrico concentrado llamado Orgendil. Como el cido ataca el algodn, el proceso debe controlarse muy cuidadosamente cada 5 o 6 segundos. Con este acabado es posible lograr los efectos de apergaminado total, apergaminado parcial y efectos pliss.
                  br
                  br
                  strong  Devorado (devor):
                  |   los efectos devorados se obtienen estampando ciertos productos qumicos sobre una tela constituida con fibras de diferentes grupos (rayn-seda), una de las fibras se destruye, dejando reas ms delgadas en el tejido dando la ilusin de transparencia. (Lupita C.L, 2008)
            .row.justify-content-center(titulo="Parte 10")
              .col-12
                .row.justify-content-center.mb-5
                  .col-lg-4
                    figure
                      img(src="@/assets/curso/tema1-imagen130.jpg")
                  .col-lg-8
                    p.mb-4.caja.temporal--rosa.py-2.px-5.text-center Otros acabados
                    p.mb-0
                      strong Blanqueado:
                      |   consiste en eliminar o decolorar las materias naturales del algodn y de la lana, obteniendo as un tejido con aspecto ms blanco. Se suelen utilizar oxidantes que reaccionan con las fibras, producindoles un aspecto mucho ms blanco.
                      br
                      br
                      strong Tintura:
                      |   consiste en transferir un color artificial al tejido, por medio de colorantes convencionales o por impregnacin pigmentaria.
                .row.justify-content-center
                  .col-lg-8
                    p.mb-4.caja.temporal--rosa.py-2.px-5.text-center Otros acabados
